
video {
    object-fit: cover;
    width: 100%;
    height: 100%;
    position: fixed;
    z-index: -1;
}

.hero-container {
    background-image: url('../../public/images/img-home.jpg') center center/cover no-repeat;
    height: 100vh;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    box-shadow: inset 0 0 0 1000px rgba(0, 0, 0, 0.2);
    object-fit: contain;
}

.hero-container > h1 {
    color: #fff;
    font-size: 100px;
    margin-top: -100px;
}

.hero-container > p {
    margin-top: 8px;
    margin-bottom: 8px;
    color: #fff;
    font-size: 32px;
}

.hero-btns {
    margin: 6px;
}

.hero-btns .btn {
    margin-left: 4px;
}

@media screen and (max-width: 960px){
    .hero-container > h1 {
        font-size: 70px;
        margin-top: -150px;
    }
}

@media screen and (max-width: 768px){
    .hero-container > h1 {
        font-size: 50px;
        margin-top: -100px;
        text-align: center;
    }

    .hero-container > p {
        font-size: 30px;
        text-align: center;
    }

    .btn-mobile {
        display: block;
    }

    .btn {
        width: 100%;
        margin-bottom: 7px;
    }
}

